.scroll-to-top {
  position: fixed;
  bottom: 30px;
  right: 30px;
  background: #4f46e5;
  color: white;
  padding: 12px;
  border-radius: 50%;
  cursor: pointer;
  z-index: 999;
  box-shadow: 0 5px 15px rgba(0,0,0,0.2);
  transition: all 0.3s ease;
}

.scroll-to-top:hover {
  background: #4338ca;
}
